body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: -webkit-gradient(linear, right top, left top, from(rgba(7, 27, 82, 1)),
      to(rgba(0, 128, 128, 1)));
  background: -webkit-linear-gradient(right, rgba(7, 27, 82, 1) 0%,
      rgba(0, 128, 128, 1) 100%);
  background: -o-linear-gradient(right, rgba(7, 27, 82, 1) 0%,
      rgba(0, 128, 128, 1) 100%);
  background: linear-gradient(to left, rgba(7, 27, 82, 1) 0%,
      rgba(0, 128, 128, 1) 100%)
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}